import controller from "../controllers/courseStructure.controller";
import { Router } from "express";
import auth from "../middleware/auth";
const router = Router();

router.get("/", controller.findAll);
router.get("/:id", controller.findOne);
router.post("/", controller.create);

router.post("/form4", controller.form4);
router.get("/form4/:courseId", controller.getform4);

router.delete("/form4/:courseId", controller.removeform4);

router.put("/form4", controller.updateform4);

router.put("/:id", controller.update);
router.delete("/:id", controller.remove);

export default router;
